/* Sticky footer */
body {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Archivo', sans-serif;
  color: #212121;
}

main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

hr {
  display: block;
  height: 2px;
  border: 0;
  border-top: 2px solid #9e9e9e;
  margin-top: 36px;
}

/* Adjust link positions to account for fixed header */
.offset:before {
	display: block;
 	content: "";
  height: 72px; /* Height of fixed navbar */
  margin-top: -72px; /* Negative margin to offset height */
  visibility: hidden;
}

@media only screen and (min-width: 720px) {
  /* Global styles */
  hr {
    margin-top: 72px;
  }
}



